define('ember-mocha/mocha-module', ['exports', 'ember', 'ember-test-helpers'], function (exports, Ember, ember_test_helpers) {

  'use strict';

  exports.createModule = createModule;

  function createModule(Constructor, name, description, callbacks, tests) {
    var module;

    if (!tests) {
      if (!callbacks) {
        tests = description;
        callbacks = {};
      } else {
        tests = callbacks;
        callbacks = description;
      }
      module = new Constructor(name, callbacks);

    } else {
      module = new Constructor(name, description, callbacks);
    }

    describe(module.name, function() {
      beforeEach(function() {
        module.setup();
        var context = ember_test_helpers.getContext();
        var keys = Ember['default'].keys(context);
        for (var i = 0; i < keys.length; i++) {
          var key = keys[i];
          this[key] = context[key];
        }
      });

      afterEach(function() {
        module.teardown();
      });

      tests();
    });
  }

});